import { IssueResponse } from '../../api/issues';
import { parseIssueFromResponse } from '../../helpers/issues';

export const updateIssue = (
  onChange: (issue: T.Issue) => void,
  resultPromise: Promise<IssueResponse>,
  oldIssue?: T.Issue,
  newIssue?: T.Issue
) => {
  const optimisticUpdate = oldIssue !== undefined && newIssue !== undefined;
  if (optimisticUpdate) {
    onChange(newIssue!);
  }

  resultPromise.then(
    response => {
      if (!optimisticUpdate) {
        const issue = parseIssueFromResponse(
          response.issue,
          response.components,
          response.users,
          response.rules
        );
        onChange(issue);
      }
    },
    () => {
      if (optimisticUpdate) {
        onChange(oldIssue!);
      }
    }
  );
};
